.Launch-rocket-name {
  font-size: 24px;
  font-weight: 700;
  color: #694ba0;
  text-transform: uppercase;
}

.Launch-when {
  position: absolute;
  top: 20px;
  right: 20px;
  color: #24b47e;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.Launch-when svg {
  margin-right: 5px;
}

.welcome {
  margin-top: -50px;
  background: -webkit-gradient(linear, left top, right top, from(#694ba0), to(#9982c4));
  background: -webkit-linear-gradient(left, #694ba0, #9982c4);
  background: -o-linear-gradient(left, #694ba0, #9982c4);
  background: linear-gradient(to right, #694ba0, #9982c4);
  height: 300px;
  -webkit-transform: skewY(-2deg);
      -ms-transform: skewY(-2deg);
          transform: skewY(-2deg);
  color: #fff;
}

.welcome-content {
  padding: 0 20px;
  padding-top: 100px;
  max-width: 580px;
  margin: 0 auto;
  -webkit-transform: skewY(2deg);
      -ms-transform: skewY(2deg);
          transform: skewY(2deg);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.welcome-title {
  font-size: 28px;
  margin: 0 10px;
  font-weight: 300;
}

.welcome-logo {
  color: #fff;
}

.welcome-logo a {
  color: #fff;
  font-weight: 600;
  font-size: 28px;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.2);
}

@media (max-width: 600px) {
  .Launch-when {
    position: unset;
    margin-top: 14px;
  }
}



