.Preloader {
  text-align: center;
}

.Preloader-line {
  width: 80%;
  height: 32px;
  background-color: #f4f4f4;
  margin-bottom: 8px;
}
.Preloader-line-small {
  width: 80px;
  height: 12px;
  background-color: #f4f4f4;
  margin-bottom: 2px;
}
.Preloader-line-medium {
  width: 220px;
  height: 18px;
  background-color: #f4f4f4;
  margin-bottom: 5px;
}

.Preloader-line-wide {
  height: 80px;
  width: 100%;
  background-color: #f9f9f9;
}



