.Stream {
  background-color: #000;
}

.launch-container {
  max-width: 816px;
  width: 100%;
  margin: 4px auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.launch-container .Card {
  max-width: calc(50% - 8px);
  -ms-flex-preferred-size: calc(50% - 8px);
      flex-basis: calc(50% - 8px);
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
}

.Launch-countdown {
  background-color: #f6f9fc;
  color: #24b47e;
  padding: 8px;
  text-align: center;
  font-size: 18px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 0 0 3px 3px;
}

.Launch-countdown svg {
  margin-right: 5px;
}

.LaunchPage-stream-container {
  max-width: 800px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  background-color: #fff;
  padding: 5px;
  border-radius: 6px;
  margin: 20px auto;
  -webkit-box-shadow: 0 2px 4px 0 rgba(50, 50, 93, 0.1);
          box-shadow: 0 2px 4px 0 rgba(50, 50, 93, 0.1);
