}

.LaunchPage-content {
  padding: 0 4px;
  margin-top: -100px;
  position: relative;
  z-index: 1;
}

.welcome-launch-content {
  padding: 0 20px;
  padding-top: 100px;
  max-width: 800px;
  margin: 0 auto;
  font-weight: 300;
  -webkit-transform: skewY(2deg);
      -ms-transform: skewY(2deg);
          transform: skewY(2deg);
  font-size: 28px;
}

.launch-back {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  color: #fff;
  font-weight: 800;
  font-size: 15px;
  margin-bottom: 10px;
}

.launch-back svg {
  margin-right: 5px;
}

@media (max-width: 700px) {
  .launch-container .Card {
    max-width: calc(100% - 8px);
    -ms-flex-preferred-size: calc(100% - 8px);
        flex-basis: calc(100% - 8px);
    -ms-flex: 0 0 auto;
        flex: 0 0 auto;
  }
}



